      width: 80%;
      left: 0;
      margin: 0 auto;
      -webkit-transform: none !important;
      -ms-transform: none !important;
      transform: none !important;
      top: 85px !important;

      li a {
        padding-left: 5px !important;
        padding-right: 10px !important;
        padding-top: 10px !important;
        padding: 10px !important;
      }
    }
  }

  padding: 18px;
  position: relative;
  text-align: center;
  background-repeat: no-repeat;
  border-bottom: 1px solid #efedf5;
  margin-top: 0px;
}

.sideuser-img img {
  width: 40px;
  height: 40px;
  border-radius: 50px;
}

.profile-img {
  position: absolute;
  top: 46px;
  right: 80px;
  display: block;
  border: 1px solid rgba(226, 212, 214, 0.7);
  border-radius: 50%;
  font-size: 10px;
  line-height: 22px;
  width: 22px;
  text-align: center;
  height: 24px;
  color: #785baa;
  background: rgba(225, 225, 225, 0.7);
}

.app-sidebar__user-avatar {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  margin-right: 15px;
}

.app-sidebar__user-name {
  &.text-sm {
    font-size: 13px;
  }

  font-size: 17px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  margin-bottom: 0;
}

.app-sidebar__user-designation {
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  margin-bottom: 0;
}

.side-menu {
  margin-bottom: 0;
  padding: 0px 0 40px 0;
  list-style: none;

  .slide {
    padding: 0;
    position: relative;
  }
}

.side-menu__item {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px 20px 10px 20px;
  font-size: 14px;
  font-weight: 400;
  border-top: 1px solid #eceef2;
}

.slide.is-expanded .side-menu__item {
  border-bottom: 1px solid #eceef2;
}

.side-menu .slide {
  &:first-child a {
    border-top: 0;
  }

  &:last-child a {
    border-bottom: 1px solid #eceef2;
  }
}

.app-sidebar .mCSB_draggerContainer {
  right: -11px;
}

.side-menu__icon {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  margin-right: 10px;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  width: 18px;
  height: 18px;
}

.side-menu__label {
  white-space: nowrap;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  font-weight: 400;
}

.slide {
  &.is-expanded [data-toggle='slide'], .side-menu__item.active {
    border-left: 0;
  }
}

.side-menu li ul {
  background: rgba(255, 255, 255, 0.04);
}

.app-sidebar .side-menu__item.active {
  font-weight: 400;
  background: transparent;
  color: #684bdb;
}

.slide-menu li.active > a {
  font-weight: 400;
  background: transparent;
  color: #684bdb !important;
}

.app-sidebar {
  .slide-menu .slide-item:hover, .side-menu__item:hover {
    color: #684bdb;
  }
}

.slide-menu li ul li a {
  font-size: 12px !important;
  font-weight: 300;
}

.app-sidebar .side-menu__item {
  &.active {
    &:hover {
      color: #684bdb !important;
    }

    .side-menu__icon {
      fill: #684bdb  !important;
    }
  }

  &:hover .side-menu__icon {
    fill: #684bdb  !important;
  }

  &.active:hover .side-menu__icon {
    color: #684bdb !important;
  }
}

.slide-menu li {
  &.active > a:hover {
    background: rgba(255, 255, 255, 0.02);
    color: #684bdb !important;
  }

  a {
    padding: 10px 10px 10px 25px;
    font-weight: 400;
  }
}

.side-menu .slide-menu li a {
  font-size: .8rem !important;
}

.slide ul li a {
  color: #8291af;
  font-weight: 400;
}

.app-sidebar {
  ul {
    li a {
      color: #14112d;
      font-weight: 400;
    }

    &.nav {
      margin-bottom: 20px;
      margin-top: 10px;
    }
  }

  .search-element {
    position: relative;

    .Search-icon {
      top: 7px !important;
    }
  }
}

.slide.is-expanded {
  .slide-menu {
    max-height: 100vh;
    -webkit-transition: max-height 2s ease;
    -o-transition: max-height 2s ease;
    transition: max-height 2s ease;
  }

  .angle {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
  }
}

.slide-menu {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: max-height 0.9s ease;
  -o-transition: max-height 0.9s ease;
  transition: max-height 0.9s ease;
  padding: 0;
  font-size: .8rem !important;
  padding-left: 0;
  list-style: none;

  li ul li a.active > a {
    font-weight: 400;
    background: transparent;
    color: #684bdb !important;
  }
}

.slide.is-expanded .slide-menu li a:hover:before {
  border-color: #727cf5 !important;
}

.slide-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 13px;
  color: #14112d;
  height: 30px;
  padding: 0 0 0 25px !important;
  position: relative;

  .icon {
    margin-right: 5px;
  }
}

.angle {
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
}

.user-info h4.font-weight-semibold {
  font-weight: 400 !important;
}

.app-sidebar .tab-content > .tab-pane {
  padding: 0;
}

.submenu-list {
  margin-left: 1.5rem;
  margin-bottom: 0;
}

.slide.submenu a {
  color: #cbd7ef;
  padding: 7px 0;
  margin: 0;
}

.toggle-menu .slide.is-expanded .angle {
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

.app-sidebar .toggle-menu .side-menu__item {
  &.active {
    color: #684bdb  !important;
    background: rgba(234, 235, 243, 0.3);
    border-radius: 3px;
  }

  &:hover {
    background: rgba(234, 235, 243, 0.3);
    color: #684bdb  !important;
  }

  &.active:hover .side-menu__icon {
    color: #684bdb  !important;
  }
}

.toggle-menu.side-menu {
  padding: 0 8px 0 8px;

  li {
    a {
      border-top: 1px solid rgba(255, 255, 255, 0.05);
    }

    ul li {
      a {
        border-top: 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.01);
        padding: 6px 7px;
        color: #000;
        font-size: 13px;
      }

      &:first-child a {
        padding-top: 20px;
      }

      &:last-child a {
        padding-bottom: 20px;
        border-bottom: 0;
      }
    }
  }
}

.app-sidebar.toggle-sidebar {
  width: 250px;
}

.user-body {
  margin-bottom: 0.5rem;
}

.user-info {
  margin-top: 10px;
  text-align: center;

  .text-dark.app-sidebar__user-name {
